import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import TermsCondition from "@screens/terms-conditions";

const Terms = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Terms and Conditions", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <TermsCondition db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  return (
    <Seo
      title="Terms and Conditions | Justtry Technologies"
      metaTitle="Terms and Conditions | Justtry Technologies"
      description="Navigate the digital landscape confidently with Justtry Technologies' meticulous legal framework. Explore our Terms and Conditions, Privacy Policy, and User Agreement, crafted for your protection. Trust Justtry Technologies - where clarity meets compliance in the online realm."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
    ></Seo>
  );
};

export default Terms;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
